import { actionTypes } from './index'

const defaultState = {
    articleList: [],
    recommendList: [],
    writerList: [],
    articlePage: 1,
};

export default (state = defaultState, action) => {
    switch(action.type) {
        case actionTypes.CHANGE_HOME_DATA: 
            return { ...state, ...action.data }
        case actionTypes.ADD_ARTICLE:
            const articleList = [ ...state.articleList, ...action.data.data.articleList ]
            return { ...state, articleList, articlePage: action.data.nextPage }
        default:
            return state;
    }
}