import React, { Component } from 'react';
import { connect } from 'react-redux';
import { actionCreators } from './store/index';
import List from './component/List';
import Recommend from './component/Recommend';
import Writer from './component/Writer';
import banner from '../../statics/image/banner.jpg';
import qr from '../../statics/image/QR.png';
import {
    HomeWrapper,
    HomeLeft,
    HomeRight,
    CodeArea,
    CodeInfo,
    BackTop,
} from './style';

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showScroll: false,
        };

        this.changeSrollShow = this.changeSrollShow.bind(this);
    }

    handleScrollTop() {
        window.scrollTo(0, 0);
    }

    render() {
        return (
            <HomeWrapper>
                <HomeLeft>
                    <img className='banner-img' alt='banner' src={ banner }/>
                    <List />
                </HomeLeft>
                <HomeRight>
                    <Recommend />
                    <CodeArea>
                        <img className='QR' alt='二维码' src={ qr } />
                        <CodeInfo>
                            <div className='title'>下载简述手机App</div>
                            <div className='desc'>随时随地发现和创作内容</div>
                        </CodeInfo>                        
                    </CodeArea>
                    <Writer />
                    { this.state.showScroll ? <BackTop onClick={() => this.handleScrollTop()}>回到顶部</BackTop> : null}
                </HomeRight>
            </HomeWrapper>
        )
    }

    componentDidMount() {
        this.props.changeHomeDataDis();
        this.bindEvents();
    }

    // 组件销毁 对应事件解绑 window全局的事件绑定去除 不影响其他组件
    componentWillUnmount() {
        window.removeEventListener('scroll', this.changeSrollShow)
    }

    bindEvents() {
        window.addEventListener('scroll', this.changeSrollShow)
    }

    changeSrollShow() {
        if (document.documentElement.scrollTop > 200) {
            this.setState({
                showScroll: true,
            });
        } else {
            this.setState({
                showScroll: false,
            });
        }
    }

}

const mapDispatchToProps = (dispatch) => {
    return {
        changeHomeDataDis() {
            dispatch(actionCreators.changeHomeData())
        }
    }
}

export default connect(null, mapDispatchToProps)(Home);