import { actionTypes } from './index'
import axios from 'axios';

const changeHomeDataAction = (result) => {
    return {
        type: actionTypes.CHANGE_HOME_DATA,
        data: {
            articleList: result.articleList,
            recommendList: result.recommendList,
            writerList: result.writerList,
        }
    }
}

const addArticleAction = (data, nextPage) => {
    return {
        type: actionTypes.ADD_ARTICLE,
        data: {
            data,
            nextPage,
        }
    }
}

export const changeHomeData = () => {
    return (dispatch) => {
        axios.get('/api/homeData.json').then((res) => {
            const result = res.data.data;
            dispatch(changeHomeDataAction(result));
        })
    }
}

export const addArticle = (page) => {
    return (dispatch) => {
        axios.get('/api/article.json?page=' + page).then((res) => {
            const list = res.data.data;
            dispatch(addArticleAction(list, page + 1));
        })
    }
}