import React, { Component } from 'react';
import { connect }  from 'react-redux';
import { actionCreators } from '../store';
import {
    ListItem,
    ListInfo,
    LoadMore
} from '../style';
import peitu from '../../../statics/image/peitu.webp'
import { Link } from 'react-router-dom';

class List extends Component {
    
    render() {
        console.log(this.props);
        const { list, page } = this.props;
        return (
            <div>
                {
                    list.map((item, index) => (
                        // 用a标签跳转不符合单页应用
                        <Link key={index} to='/detail'>
                            <ListItem>
                                <img className='pic' alt='文章配图' src={peitu} />
                                <ListInfo>
                                    <h3 className='title'>{item.title}</h3>
                                    <p className='desc'>{item.desc}</p>
                                </ListInfo>
                            </ListItem>
                        </Link>
                    ))
                }
                <LoadMore onClick={() => this.props.loadMore(page)}>加载更多</LoadMore>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        list: state.home.articleList,
        page: state.home.articlePage,
    }
} 

const mapDispatchToProps = (dispatch) => {
    return {
        loadMore(page) {
            dispatch(actionCreators.addArticle(page))
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(List);