import styled from 'styled-components';

export const HomeWrapper = styled.div`
    width: 960px;
    margin: 0 auto;
`

export const HomeLeft = styled.div`
    float: left;
    margin-left: 15px;
    padding-top: 30px;
    width: 625px;
    .banner-img {
        width: 625px;
        height: 270px;
    }
`

export const HomeRight = styled.div`
    float: right;
    width: 240px;
`

export const ListItem = styled.div`
    overflow: hidden;
    padding: 20px 0;
    border-bottom: 1px solid #dcdcdc;
    .pic {
        width: 125px;
        height: 100px;
        float: right;
        border-radius: 10px;
    }
`

export const ListInfo = styled.div`
    width: 500px;
    float: left;
    .title {
        line-height: 27px;
        font-size: 18px;
        font-weight: bold;
        color: #333;
    }
    .desc {
        line-height: 24px;
        font-size: 13px;
        color: #999;
    }
`

export const Rec = styled.div`
    margin-top: 30px;
`

export const RecItem = styled.div`
    .recImg {
        width: 280px;
        height: 50px;
        margin-bottom: 6px;
        border-radius: 4px;
        vertical-align: middle;
    }
`

export const CodeArea = styled.div`
    width: 234px;
    height: 60px;
    padding: 10px 22px;
    border: 1px solid #f0f0f0;
    .QR {
        width: 60px;
        height: 60px;
        opacity: .85;
    }
`

export const CodeInfo = styled.span`
    margin-top: 8px;
    float: right;
    .title {
        font-size: 15px;
        color: #333;
    }
    .desc {
        margin-top: 4px;
        font-size: 13px;
        color: #999;
    }
`

export const WriterWrapper = styled.div`
    width: 280px;
    height: 400px;
    margin: 10px 0 20px;
    .writer {
        font-size: 14px;
        color: #969696;
    }
`

export const WriterItem = styled.div`
    overflow: hidden;
    width: 280px;
    height: 48px;
    margin-top: 10px;
    .pic {
        float: left;
        width: 48px;
        height: 48px;
        border-radius: 50%;
        margin-left: 5px;
    }
    .name {
        padding-top: 5px;
        margin-left: 72px;
        font-size: 14px;
        display: block;
        text-decoration: none;
        color: #333;
        cursor: pointer;
    }
    .info {
        margin-top: 2px;
        margin-left: 20px;
        font-size: 12px;
        color: #969696;
    }
    .pick {
        float: right;
        margin-top: 0px;
        padding: 0;
        font-size: 13px;
        color: #42c02e;
    }
`

export const LoadMore = styled.div`
    width: 50%;
    height: 40px;
    line-height: 40px;
    margin: 30px auto;
    background: #a5a5a5;
    text-align: center;
    border-radius: 20px;
    color: #fff;
    cursor: pointer;
`

export const BackTop = styled.div`
    position: fixed;
    bottom: 100px;
    right: 100px;
    width: 60px;
    height: 60px;
    background: #eee;
    border: 1px solid #ccc;
    corlor: #ccc;
    font-size: 10px;
    line-height: 60px;
    text-align: center;
    cursor: pointer;
`