import React, { Component } from 'react';
import { connect } from 'react-redux';
import { 
    WriterWrapper,
    WriterItem
} from '../style'; 
import touxiang from '../../../statics/image/touxiang.webp'

class Writer extends Component {
    render() {
        const { list } = this.props; 
        return (
            <WriterWrapper>
                <span className='writer'>推荐作者</span>
                {
                    list.map((item, index) => (
                        <WriterItem key={index}>
                            <img className='pic' alt='头像' src={touxiang} />
                            <a className='name' href='/'>{ item.name }</a>
                            <span className='info'>{ item.info }</span>
                            <span className='pick'>+关注</span>
                        </WriterItem>
                    ))
                }
            </WriterWrapper>
        )
    }
};

const mapStateToProps = (state) => {
    return {
        list: state.home.writerList
    }
}

export default connect(mapStateToProps, null)(Writer);